<template>
  <div>
      <head-top signin-up="msite">
          <router-link :to="'/search/geohash'" slot="search" class="link_search">
            <svg width="100%" height="100%" xmlns="http://www.w3.org/2000/svg" version="1.1">
	    			<circle cx="8" cy="8" r="7" stroke="rgb(255,255,255)" stroke-width="1" fill="none"/>
	    			<line x1="14" y1="14" x2="20" y2="20" style="stroke:rgb(255,255,255);stroke-width:2"/>
	    		</svg>
          </router-link>
          <router-link :to="'/home'" slot="msite-title" class="msite_title">
              <span class="title_text ellipsis">{{msitetitle}}</span>
          </router-link>
      </head-top>
      <nav class="msite_nav">
          <div class="swiper-container" v-if="foodTypes.length">
              <div class="swiper-wrapper">
                  <div class="swiper-slide food_types_container" v-for="(item,index) in foodTypes" :key="index">
                      <router-link :to="{path:'/food',query:{geohash,title:footItem.title,restaurant_category_id:getCategoryId(footItem.link)}}" v-for="footItem in item" :key="footItem.id" class="link_to_food">
                        <figure  >
                            <img :src="imgBaseUrl+footItem.image_url" alt="">
                            <figcaption>{{footItem.title}}</figcaption>
                        </figure>
                      </router-link>
                  </div>
              </div>
              <div class="swiper-pagination"></div>
          </div>
          <!--  -->
          <img src="" alt="" v-else class="f1_back animation_opacity">
      </nav>
        <div class="top_fujin">
            <header class="heade_fujin">
                <svg class="shop_icon">
	    			<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#shop"></use>
	    		</svg>
                <span class="shop_text"></span>
            </header>
            <shop-list v-if="hasGetData" :geohash="geohash"></shop-list>
        </div>
        <foot-guide></foot-guide>
  </div>
</template>

<script type="text/ecmascript-6">
import headTop from '../../components/header/header';
import Swiper from 'swiper';
import footGuide from '../../components/footer/footGuide'
import 'swiper/dist/css/swiper.min.css';
import {msiteFoodTypes,cityGuess,msiteAddress} from '../../service/getData'
import {mapMutations} from 'vuex'
import shopList from '../../components/common/shoplist'
export default {
    data()
    {
        return{
            foodTypes:[],//
            geohash:'',//city
            msitetitle:'...',//msite
            hasGetData:false,//,
            imgBaseUrl:'https://fuss10.elemecdn.com'//
        }
    },components: {
        headTop,
        shopList,
        footGuide
    },
    async beforeMount () {//
        //
        if(!this.$route.query.geohash)
        {
            const adders=await cityGuess();
            this.geohash=adders.latitude+','+adders.longitude;
        }else{
            this.geohash=this.$route.query.geohash;
        }
        //geohashvuex
        this.SAVE_GEOHASH(this.geohash);
        //
        let res=await msiteAddress(this.geohash);

        this.msitetitle=res.name;
        //alert(this.msitetitle);
        //vuex
        this.RECORD_ADDRESS(res);
        //
        this.hasGetData=true;
    },
    mounted () {//DOM
     msiteFoodTypes(this.geohash).then(res=>{
         let reslength=res.length;
         let resArr=[...res];//
        let footArr=[];
        for(let i=0,j=0;i<reslength;i+=8,j++)
        {
            footArr[j]=resArr.splice(0,8);//8
        }
        this.foodTypes=footArr;
     }).then(()=>{
         //swiper
         
       var mySwiper = new Swiper('.swiper-container', {
          pagination:'.swiper-pagination',
          loop:true
    
       })
     })  
    },methods: {
        ...mapMutations([
            'SAVE_GEOHASH','RECORD_ADDRESS'
        ]),
        getCategoryId(url)
       {
           //decodeURIComponenturl
			//ulr.split= replace&target_name
			// /restaurant_category_id/gi.test(urlData)
			//JSON.parse(urlData).restaurant_category_id.id
           let urlDate=decodeURIComponent(url.split('=')[1].replace('&target_name',''));
           if(/restaurant_category_id/gi.test(urlDate))
           {
               return JSON.parse(urlDate).restaurant_category_id.id;
           }else{
               return '';
           }
       } 
    }
}
</script>

<style lang="scss" scoped>
    @import 'src/style/mixin';
	.link_search{
		left: .8rem;
		@include wh(.9rem, .9rem);
		@include ct;
	}
	.msite_title{
		@include center;
        width: 50%;
        color: #fff;
        text-align: center;
        margin-left: -0.5rem;
        .title_text{
            @include sc(0.8rem, #fff);
            text-align: center;
            display: block;
        }
	}
    .msite_nav{
        background: #fff;
        padding-top: 2.1rem;
        border-bottom: 0.025rem solid $bc;
        height: 10.6rem;
        .swiper-container{
            @include wh(100%,auto);
            padding-bottom: 0.25rem;
            .swiper-pagination{
                bottom: 0.2rem;
            }
        }
        .fl_back{
			@include wh(100%, 100%);
		}
        
    }
    .food_types_container{
        display: flex;
        flex-wrap: wrap;//
        .link_to_food{
            width: 25%;
            padding:0.3rem 0rem;
            @include fj(center);//
            figure{
                img{
                    @include wh(1.8rem,1.8rem);
                }
                figcaption{
                    text-align: center;
                    @include sc(0.55rem,#666);
                }
            }
        }
    }
    .top_fujin{
        margin-top: 0.4rem;
        background: #fff;
        border-top: 1px solid $bc;
        .heade_fujin{
            padding-left:0.85rem;
            .shop_icon{
                fill: #888;
                @include wh(0.7rem,0.7rem);
                vertical-align: middle;//
            }
            .shop_text{
                @include font(0.55rem,1.6rem);
                color:#888;
            }
        }
    }

</style>



